.team-container {
    display: grid;
    grid-gap: 40px;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-auto-rows: 500px;
    flex: 1;
    margin-left: 50px;
    margin-right: 50px;
}

.team-image {
    border-radius: 50%;
    height: 250px;
    margin-left: auto;
    margin-right: auto;
    display: block;
}

.team-name {
    display: block;
    margin: auto;
    padding: 20px;
    text-align: center;
    font-size: 20px;
}

.team-description {
    display: block;
    margin: auto;
    width: 80%;
    padding: 5px;
    text-align: center;
    font-size: 18px;
}