.public-container > div {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2em;
    color: #20263f;
}
.public-container > div > img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.public-container {
    display: grid;
    grid-gap: 40px;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-auto-rows: 500px;
    flex: 1;
    margin-left: 50px;
    margin-right: 50px;
}

.public-container > div:hover {
    transform: scale(1.1);
}
.public-container > div:hover .description {
    display: block;
    text-align: center;
}

.public-container > div:hover img {
    opacity: 0.7;
}